@import "variables.scss";

.nav {
  //background: yellowgreen;
  height: 79px;
  width: calc(100% - 10%);
  padding: 0 5%;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
  border-bottom: 1px solid navy;
  background: whitesmoke;
  z-index: 10;

  &__userInfo {
    display: flex;
    flex-direction: row;
    place-items: center;
    margin-left: auto;
    position: relative;
  }

  &__userName {
    margin: 0 1rem;
    font-weight: normal;
  }

  &__userPanel {
    position: absolute;
    top: 115%;
    right: 0;
    width: 100%;
    display: flex;
    flex-direction: row;
    z-index: 15;

    &--show {
      display: block;
      //background: rgba(0, 0, 0, 0.5);
      color: white;
    }

    &--hidden {
      display: none;
    }
  }

  &__logout {
    margin-left: auto;
    width: 100%;
    padding: 0.5rem 2rem;
    font-size: $normalFontSize;
    outline: none;
    border: none;
    border-radius: 0.5rem;
    background: rgb(34, 37, 110);
    background: linear-gradient(
      90deg,
      rgba(34, 37, 110, 1) 0%,
      rgba(75, 81, 241, 1) 100%
    );
    color: white;

    &:hover {
      cursor: pointer;
      background: rgb(45, 49, 144);
      background: linear-gradient(
        90deg,
        rgba(45, 49, 144, 1) 0%,
        rgba(107, 112, 249, 1) 100%
      );
    }
  }
}
