@import url("https://fonts.googleapis.com/css2?family=Rock+Salt&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@300;400;700&display=swap");

$mainColor: #22256e;
$secondMainColor: #f87070;
$hoverColor: #353899;

$h1FontSize: calc(32px + 1vw);
$h2FontSize: calc(24px + 1.5vw);
$h3FontSize: calc(18px + 0.7vw);
$h4FontSize: calc(16px + 0.5vw);
$h5FontSize: calc(13px + 0.3vw);
$h6FontSize: calc(10px + 0.3vw);

$normalFontSize: calc(16px + 0.1vw);

* {
  margin: 0;
  padding: 0;
  font-family: "Source Sans Pro", sans-serif;
}

h1 {
  font-size: $h1FontSize;
  font-size: $normalFontSize;
}

h2 {
  font-size: $h2FontSize;
}

h3 {
  font-size: $h3FontSize;
}

h4 {
  font-size: $h4FontSize;
}

h5 {
  font-size: $h5FontSize;
}

h6 {
  font-size: $h6FontSize;
}

p {
  font-size: $normalFontSize;
}

.gran {
  color: $mainColor;
}

.pinky {
  color: $secondMainColor;
}
