@import "variables.scss";

.chat {
  width: 100%; //calc(75% - 1px);;
  height: 100%;
  //border-left: 1px solid navy;
  display: flex;
  flex-direction: column;
  z-index: 50;

  &__header {
    width: 100%;
    padding: 1rem 0;
    display: grid;
    place-items: center;
    display: none;
  }

  &__roomInfo {
    display: flex;
    flex-direction: row;
    place-items: center;
  }

  &__roomName {
    margin-left: 1rem;
    font-weight: normal;
  }

  &__messages {
    padding: 0 0;
    padding-top: 10px;
    overflow: hidden;
    overflow-y: scroll;
  }

  &__footer {
    width: 80%;
    height: 79px;
    padding: 0 10%;
    display: flex;
    flex-direction: row;
    place-items: center;
    background: whitesmoke;
    border-top: 1px solid navy;
  }

  &__form {
    width: 100%;
    display: flex;
    flex-direction: row;
    place-items: center;
  }

  &__input {
    width: 100%;
    font-size: $normalFontSize;
    background: none;
    border: none;
    outline: none;
    color: black;
    border-bottom: 1px solid gray;
    padding: 0.5rem 1rem;
  }

  &__submit {
    margin-left: 1rem;
    background: none;
    border: none;
    outline: none;

    &:hover {
      cursor: pointer;
      color: rgb(49, 49, 49);
    }
  }
}
