@import "variables.scss";

.homePage {
  display: flex;
  flex-direction: row;
  //min-height: 500px;

  &__left {
    width: 40%;
    padding: 0 10%;
    display: grid;
  }

  &__title {
    font-size: $h1FontSize;
    font-weight: normal;
    margin-top: 20%;
  }

  &__infos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    h4 {
      font-size: $h4FontSize;
      font-weight: normal;
    }
  }

  &__linkWrapper {
    display: grid;
    align-items: center;
    justify-content: center;

    a {
      background: rgb(34, 37, 110);
      background: linear-gradient(
        90deg,
        rgba(34, 37, 110, 1) 0%,
        rgba(75, 81, 241, 1) 100%
      );
      color: white;
      text-decoration: none;
      padding: 1rem 4rem;
      border-radius: 4rem;
      font-size: $normalFontSize;

      &:hover {
        background: rgb(45, 49, 144);
        background: linear-gradient(
          90deg,
          rgba(45, 49, 144, 1) 0%,
          rgba(107, 112, 249, 1) 100%
        );
        //font-weight: bold;
      }
    }
  }

  &__right {
    width: 40%;
    display: grid;
    align-items: center;
    justify-items: center;
    background-color: $mainColor;
    -webkit-clip-path: ellipse(64% 92% at 65% 50%);
    clip-path: ellipse(64% 92% at 65% 50%);
    z-index: -100;
  }

  &__typing {
    width: 25vw;
  }
}

@media screen and (max-width: 970px) {
  .homePage {
    //height: calc(100vh - 120px);
    &__left {
    }
    &__infos {
      grid-template-columns: repeat(1, 1fr);
    }
    &__linkWrapper {
      a {
        padding: 1rem 2rem;
      }
    }
    &__right {
    }
  }
}

@media (max-width: 767.98px) {
  .homePage {
    &__left {
      width: 100%;
    }
    &__infos {
      grid-template-columns: repeat(1, 1fr);
    }
    &__linkWrapper {
      a {
        padding: 1rem 2rem;
      }
    }
    &__right {
      width: 30%;
    }
    &__typing {
      display: none;
    }
  }
}

@media (max-height: 600px) {
  .homePage {
    &__left {
    }
    &__infos {
    }
    &__linkWrapper {
    }
    &__right {
    }
    &__typing {
      display: none;
    }
  }
}

@media (orientation: landscape) {
  .homePage {
    .homePageLeft {
      h1 {
        margin-top: 10%;
      }
      .infos {
        grid-template-columns: repeat(2, 1fr);
      }
    }
    .homePageRight {
    }
  }

  .homePage {
    &__left {
    }
    &__title {
      margin-top: 10%;
    }
    &__infos {
      grid-template-columns: repeat(2, 1fr);
    }
    &__linkWrapper {
    }
    &__right {
    }
    &__typing {
    }
  }
}
