@import "variables.scss";

.message {
  display: flex;
  place-items: center;
  padding: 2rem 2.5%;
  flex-direction: row;

  &__text {
    background: $secondMainColor;
    padding: 1rem 2rem;
    border-radius: 1rem;
    max-width: 100%;
    position: relative;
    bottom: $h5FontSize;
    text-align: justify;
    margin-left: 1rem;
    border-bottom-left-radius: 0;
  }

  &__timestamp {
    position: absolute;
    width: 100vw;
    top: 101%;
    left: 0;
  }

  &__username {
    position: absolute;
    bottom: 102%;
    left: 0;
  }
}

@media screen and (max-width: 960px) {
}
