@import "variables.scss";

.login {
  text-align: center;
  display: grid;
  place-items: center;

  &__svg {
    width: 60%;
    height: auto;
  }

  &__form {
    //background: salmon;
    height: 100%;
    display: grid;
    place-items: center;
  }

  &__input {
    width: 40vw;
    height: 2rem;
    border: none;
    outline: none;
    border-bottom: 1px solid lightgrey;
    background-color: inherit;
    //margin-top: 2rem;
    font-size: $normalFontSize;
  }

  &__submit {
    font-size: $normalFontSize;
    //margin-top: 3rem;
    width: 40%;
    padding: 0.75rem 0;
    border-radius: 4rem;
    background: rgb(34, 37, 110);
    background: linear-gradient(
      90deg,
      rgba(34, 37, 110, 1) 0%,
      rgba(75, 81, 241, 1) 100%
    );
    border: none;
    color: white;
    text-transform: uppercase;
    outline: none;

    &:hover {
      cursor: pointer;
      background: rgb(45, 49, 144);
      background: linear-gradient(
        90deg,
        rgba(45, 49, 144, 1) 0%,
        rgba(107, 112, 249, 1) 100%
      );
    }
  }
}

@media screen and (max-width: 960px) {
  .login {
    &__input {
      width: 70vw;
    }
  }
}
